<template>
	<div class="myaddress">
		<div class="myaddress_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="myaddress_title">我的收货地址</p>
		</div>
		<van-address-list
		  :switchable="false"
		  :list="list"
		  default-tag-text="默认"
		  @add="onAdd"
		  @edit="onEdit"
		  @click-item="clickitem"
		/>
	</div>
</template>

<script>
	export default{
		name:'myaddress',
		data(){
			return{
				list: [],
				addList:[],
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.userAddressList();
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			clickitem(item,index){
				if(this.$route.query.type == 0){
					var addressObj = JSON.stringify(item);
					localStorage.setItem("addressObj",addressObj);
					this.goBack();
				}
				
			},
			onAdd() {
				let _this = this;
			  _this.$router.push({path:'/addAddress'})
			},
			onEdit(item, index) {
				let _this = this;
			  _this.$router.push({path:'/addAddress',query:{item:_this.addList[index]}})
			},
			userAddressList(){
				let _this = this;
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/user/address/list',
					data:redata,
				}).then(function(res){
					console.log(res);
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.addList = reData;
						for(var i in reData){
							var listItem = {};
							listItem.id = reData[i].id;
							listItem.name = reData[i].name;
							listItem.tel = reData[i].phone;
							listItem.address = reData[i].province+reData[i].city+reData[i].county+reData[i].detailAddress;
							listItem.isDefault = !!!reData[i].defaultAddress;
							_this.list.push(listItem)
						}
					}
				})
			}
		}
	}
</script>

<style scoped>
	.myaddress{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fcebfe;
		padding-bottom:0.8rem;
	}
	.myaddress_header{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.3rem;
		padding-right:0.3rem;
		position: relative;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../static/img/icon_arrow_left.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
	  z-index: 4;
	}
	.myaddress_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		z-index: 3;
	}
</style>
